* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.main_div {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: aquamarine;
}

.box {
  padding: 20px;
  background-color: white;
  width: 300px;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 5px;
  box-shadow: 5px 5px 5px rgb(90, 194, 161);
}

h1 {
  font-weight: 900;
  color: grey;
  font-size: 40px;
  text-shadow: 4px 4px 4px rgba(70, 69, 69, 0.26);
}

.row {
  padding: 10px 0px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

input {
  width: 80%;
  padding-left: 20px;
  border: none;
  border-radius: 5px;
  color: grey;
}

input:hover {
  background-color: rgba(212, 209, 209, 0.719);
  font-size: 15px;
}

.btn_add {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: none;
}

.btn_add:hover {
  background-color: rgb(194, 190, 190);
  color: rgb(92, 165, 92);
  font-size: 20px;
}

ul {
  width: 100%;
  color: grey;
  font-size: 20px;
}

li {
  display: flex;
  align-items: center;
  padding-left: 5px;
}

li:hover {
  background-color: rgb(216, 213, 213);
  color: white;
  border-radius: 10px;
}

.btn_delete {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: none;
  margin-right: 20px;
  color: red;
}

.btn_delete:hover {
  background-color: red;
  font-size: 15px;
  width: 18px;
  height: 18px;
  color: black;
}
